import { Component, OnInit, OnDestroy } from '@angular/core';

import { EventService } from '@app/core/services/events';

@Component({
  selector: 'app-preloader',
  template: `
    <div *ngIf="!appLoaded" class="app-loader-wrapper preloader">
      <div class="app-loader">
        <span class="logo"><img src="/assets/images/logo/pahal-soft.png" alt="Pahal Soft" /></span>
        <svg class="circular" viewBox="25 25 50 50">
          <circle class="path" cx="50" cy="50" r="20" fill="none" stroke-width="2" stroke-miterlimit="10"/>
        </svg>
      </div>
    </div>
  `
})
export class PreloaderComponent implements OnInit, OnDestroy {

  public appLoaded: boolean;
  private subs: any;

  constructor(
    private appEvents: EventService
  ) {
    this.appLoaded = false;
  }

  ngOnInit() {
    this.subs = this.appEvents.appLoaded.subscribe((event) => this.appLoaded = event);
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }
}
