import {HttpClient, HttpParams} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {Observable, of} from 'rxjs';

import {AppSettings} from '@app/app.constants';

@Injectable()
export class HttpService {
  private API_PREFIX: string;
  private baseApi: string;

  constructor(
    private http: HttpClient,
    private appSettings: AppSettings
  ) {
    this.API_PREFIX = this.appSettings.API_PREFIX;

    this.baseApi = `${this.API_PREFIX}`;
  }


  get<T>(path: string, search: HttpParams = null): Observable<T> {
    const options = {params: search};
    this.prepareOptions(options);
    const url = `${this.baseApi}${path}`;
    return this.http.get(url, options) as Observable<T>;
  }

  post<T>(path: string, data: any = null): Observable<T> {
    const options = {headers: {}};
    this.prepareOptions(options);

    let body = data;
    if (data instanceof Object) {
      body = JSON.stringify(data);
      options.headers['Content-Type'] = 'application/json';
    }
    const url = `${this.baseApi}${path}`;
    return this.http.post(url, body, options) as Observable<T>;
  }

  delete<T>(path: string): Observable<T> {
    const options = {};
    this.prepareOptions(options);
    const url = `${this.baseApi}${path}`;
    return this.http.delete(url, options) as Observable<T>;
  }

  put<T>(path: string, data: any): Observable<T> {
    const options = {headers: {}};
    this.prepareOptions(options);
    if (data) {
      options.headers['Content-Type'] = 'application/json';
    }
    const url = `${this.baseApi}${path}`;
    return this.http.put(url, JSON.stringify(data), options) as Observable<T>;
  }

  patch<T>(path: string, data: any): Observable<T> {
    const options = {headers: {}};
    this.prepareOptions(options);
    if (data) {
      options.headers['Content-Type'] = 'application/json';
    }
    const url = `${this.baseApi}${path}`;
    return this.http.patch(url, JSON.stringify(data), options) as Observable<T>;
  }

  getParam(params: HttpParams, param: string, value: string | number): HttpParams {
    if (value) {
      return params.set(param, value.toString());
    }
    return params;
  }

  private prepareOptions(options: any) {
    if (!options.headers) {
      options.headers = {};
    }
    options.headers['Accept'] = 'application/json';
  }

}
