import { Component, OnInit, AfterViewInit, ViewChild, ElementRef } from '@angular/core';
import { Router } from '@angular/router';

import ImageEditor from 'tui-image-editor';

import * as jquery from 'jquery';

import { AppSettings } from '@app/app.constants';
import { EventService } from '@app/core/services/events';


@Component({
  selector: 'app-editor',
  templateUrl: './editor.component.html',
  styleUrls: [
    './editor.component.scss'
  ],
})
export class EditorComponent implements OnInit, AfterViewInit {
  @ViewChild('imageEditor', { static: true }) editorRef: ElementRef;

  constructor(
    private router: Router,
    private appEvents: EventService,
    public appSettings: AppSettings
  ) {

  }

  ngOnInit() {
    this.appEvents.appLoaded.next(true);
  }

  ngAfterViewInit() {
    const W = jquery(this.editorRef.nativeElement).width();
    const H = jquery(this.editorRef.nativeElement).height();
    const instance = new ImageEditor(this.editorRef.nativeElement, {
      includeUI: {
        initMenu: 'filter',
        menuBarPosition: 'left'
      },
      usageStatistics: false,
      cssMaxWidth: W,
      cssMaxHeight: H,
      selectionStyle: {
        cornerSize: 20,
        rotatingPointOffset: 70
      }
    });
  }

}
